import * as React from "react";
import Layout from "@layout";
import SEO from "@components/seo";
import HeroOne from "../container/home-one/hero";
import TredingArticle from "../container/home-one/trending-article";
import FromFollowingArea from "../container/home-one/from-following";
import TrendingTopics from "../container/home-one/trending-topic";
import FeaturedVideoArea from "../container/home-one/featured-video";
import AddBanner from "../container/home-one/add-banner";
import { Container, Button } from "react-bootstrap"
import Compass from '../components/compass/LuoPan'
import Overlay from '../components/compass/Overlay'
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'
import DropZone from '../components/dropzone/DropzoneImages'
import Editor from '../components/Editor'

const { useState } = React
const IndexPage = () => {
    const [compassFacing, setCompassFacing] = useState(TWENTY_FOUR_MOUNTAIN[13]);
    const [blob, setBlob] = useState(null);

    const onUpload = (fileWithMeta, status) => {
        const { file } = fileWithMeta;
        const reader = new FileReader()
        reader.onload = () => {
            setBlob(btoa(reader.result))
            //dispatch(setContent(null))
        }
        reader.readAsBinaryString(file)
    }

    return (
        <Layout>
            <SEO title="Home" pathname="/" />
            <Container className="p-4 fengshui-container">
                <h2>Master Ni Feng Shui Method</h2>
                <p>
                    The gist of Master Ni Method is that depending on your position in the family, you need to have your room located in the correct sector.
                    <br />
                    When that is done, you will have the correct thoughts as should be by your position, and all things will follow.
                
                </p>
                <ul>
                    <li>

                    </li>
                </ul>

                {
                    blob && <Editor
                        background={<img src={`data:image/jpeg;base64,${blob}`} alt="image" />}
                    />
                }

                {
                    blob && <div className="compass-container">
                        <Overlay>
                            <Compass
                                facing={compassFacing}
                            />
                        </Overlay>
                    </div>
                }


                {
                    !blob && <DropZone
                        multiple={false}
                        inputContent="Upload Floor Plan"
                        onChangeStatus={onUpload}
                    />
                }
            </Container>

        </Layout>
    );
};

export default IndexPage;
